*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
.service-body{
    background-image: black;
    height: 100%;
    justify-content:center;
    align-items: center;
    min-height: 100vh;
}
.converter-body{
    background: black;
    color: white;
    display: flex;
    height: 90vh;
}
.highlight{
    background-image: linear-gradient(to right ,
    #f9989f,#fccb8f,#faf096);
    border-radius: 6px;
    padding: 3px 6px;
}
.container{
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
}
.container .coin-price{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 20px;
    border-radius: 5px;
    box-shadow: 0 0 3px #ccc;
    margin: 7px;
}

.container .coin-price .logo{
    width: 70px;
    margin-right: 10px;
}
.container .coin-price .logo img{
    width: 100%;
}
.container .coin-price div{
    display: block;
}
.container .coin-price div h1{
    font-size: 20px;
}