*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}


.content{
  height: 100vh;
  background: linear-gradient(rgb(101, 94, 94),rgb(67, 47, 94),rgb(65, 37, 93));
}

.register-box{
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 50px;
  border-radius: 30px 5px 30px 5px;
  box-shadow: 7px 7px 15px #bbcfda,-4px -4px 13px #fff  , inset 4px 4px 8px rgba(209, 217, 230, 0.2),  inset -8px -8px 8px rgba(255, 255, 255, 0.2);
                border: 1px solid rgba(255, 255, 255, 0.3); 
                background: #c2c1c1;
}
.register-page{
   display: flex;
   align-items: center;
   justify-content: center;
   height: 100vh;
   background: radial-gradient(rgb(74, 13, 54),rgb(19, 2, 27),rgb(2, 0, 2));
}
.register-box Form h1 {
   font-family: 'Bricolage Grotesque', sans-serif;
font-family: 'Montserrat', sans-serif;
font-family: 'Roboto Mono', monospace;
   color: rgb(58, 13, 74);
   margin-bottom: 5vh;
   font-weight: 900;
}
.register-box Form label {
   font-size: large !important;
   color: rgb(74, 13, 54) !important;
   font-weight: 400;
}
.register-box Form Input {
   box-shadow: 1px 1.5px 1px 1px rgba(34, 0, 59, 0.25) !important;
}
.to-register {
   color: #187388 !important;
}
.register-btn {
   display: inline-block;
   outline: 0;
   cursor: pointer;  
   border-radius: 10px;
   border: 2px solid #140320;
   color: rgb(58, 13, 74);
   padding: 8px;
   background: #c2c1c1;
   font-weight: 600;
   font-size: 16px;
   height: 42px;
   box-shadow: 6px 2px 16px 0px rgba(39, 38, 38, 0.48) , -6px -2px 16px 0px rgba(167, 167, 167, 0.8);  
}

