.f-container{
   flex: 0 1 800px;
   margin-left: auto;
   margin-right: auto;
   padding: 30px;
   margin-top: 150px;
}
.screen{
   position: relative;
   background: black;
   border-radius: 15px;
   box-shadow: 0 20px 40px rgba(0, 0, 0, 0.493), 0 15px 15px rgba(0, 0, 0, 0.445);
}
.screen::after{
   content: "";
   display: block;
   position: absolute;
   top: 0;
   left: 20px;
   right: 20px;
   bottom: 0;
   border-radius: 15px;
   box-shadow: 0 20px 40px rgba(0, 0, 0, 0.4);
   z-index: -1;
}
.screen-body{
   display: flex;

}
.screen-body-item{
   flex: 1;
   padding: 50px;
}
.screen-body-item.left{
   display: flex;
   flex-direction: column;
}
.title{
   display: flex;
   flex-direction: column;
   position: relative;
   color: red;
   font-size: 26px;
  letter-spacing: 1px;
}
.title::after{
   content: "";
   display: block;
   position: absolute;
   left: 0;
   bottom: -12px;
   width: 50px;
   height: 3px;
   background: red;
}
.contact{
   margin-top: auto;
   font-size: 14px;
   color: red;

}
.form-group{
   margin-bottom: 15px;
}
.form-group.message{
