   background: none;
   border:none;
   border-bottom: 1px solid grey;
   color: white;
   font-size: 14px;
   outline:none;
   transition: border-color 0.2s;

}
.form-control::placeholder {
   color: rgb(163, 163, 163);

}
.form-control:focus{
   border-bottom-color: rgb(163, 163, 163);
}
.form-button{
   background: none;
   border: 1px solid;
   color: red;
   font-size: 14px;
   cursor: pointer;
   outline: none;
   padding: 10px 20px;
   margin-right: 15px;
   border-radius:10px ;
   margin-top: 15px;
}
.form-button:hover{
   color: rgb(255, 0, 212);
}
