   background: #5BB318;
   color: #ffffff;
}
.navbar-dropdown li a{
   background: #5BB318;
}
nav ul li a:not(:only-child):after, nav ul li a:visited:not(:only-child):after {
   padding-left: 4px;
   content: ' \025BE';
}
nav ul li ul li {
   min-width: 190px;
}
nav ul li ul li a {
   padding: 15px;
   line-height: 20px;
}
.navbar-dropdown {
   position: absolute;
   display: none;
   z-index: 1;
   background: #fff;
   box-shadow: 0 0 35px 0 rgba(0,0,0,0.25);
}
/* Mobile navigation */
.nav-mobile {
   display: none;
   position: absolute;
   top: 0;
   right: 0;
   background: transparent;
   height: 55px;
   width: 70px;
}
@media only screen and (max-width: 800px) {
   .nav-mobile {
       display: block;
  }
   nav {
       width: 100%;
       padding: 55px 0 15px;
  }
   nav ul {
       display: none;
  }
   nav ul li {
       float: none;
  }
   nav ul li a {
       padding: 15px;
       line-height: 20px;
       background: #262626;
  }
   nav ul li ul li a {
       padding-left: 30px;
  }
   .navbar-dropdown {
       position: static;
}
@media screen and (min-width:800px) {
   .nav-list {
       display: block !important;
  }
}
#navbar-toggle {
   position: absolute;
   left: 18px;
   top: 15px;
   cursor: pointer;
   padding: 10px 35px 16px 0px;
}
#navbar-toggle span, #navbar-toggle span:before, #navbar-toggle span:after {
   cursor: pointer;
   border-radius: 1px;
   height: 3px;
   width: 30px;
   background: #ffffff;
   position: absolute;
   display: block;
   content: '';
   transition: all 300ms ease-in-out;
}
#navbar-toggle span:before {
   top: -10px;
}
#navbar-toggle span:after {
   bottom: -10px;
}
#navbar-toggle.active span {
   background-color: transparent;
}
#navbar-toggle.active span:before, #navbar-toggle.active span:after {
   top: 0;
}
#navbar-toggle.active span:before {
   transform: rotate(45deg);
}
#navbar-toggle.active span:after {
   transform: rotate(-45deg);
}}
